<template>
  <div>
    <HeaderCom></HeaderCom>
    <div class="main-container">
      {{text}}
    </div>
    <BottomTab></BottomTab>
  </div>
</template>

<script>
  import HeaderCom from '@/components/header'
  import BottomTab from '@/components/bottomTab'
  export default {
    name: "index",
    components: {
      HeaderCom,
      BottomTab
    },
    data () {
      return {
       text: ''
      }
    },
    mounted () {
      apiready = function () {
        this.text = '测试数据'
      }
    }
  }
</script>

<style lang="less">
.header-container {
  height: 44px;
}
</style>



